.cl-btn-6 {
  width: 24px;
  cursor: pointer;
  margin-left: auto;
  position: absolute;
  top: 20px;
  right: 0;
}

.cl-btn-6-in {
  width: inherit;
  text-align: center;
}

.cl-btn-6-txt {
  text-transform: uppercase;
  color: #898b8e;
  opacity: 0;
  cursor: pointer;
  font-family: Verdana;
  font-size: 5px;
  font-weight: bold;
  line-height: 16px;
  transition: all .3s ease-in;
}

.cl-btn-6-in:before, .cl-btn-6-in:after {
  content: "";
  height: 4px;
  width: inherit;
  background: #636669;
  transition: all .3s ease-in;
  position: absolute;
  left: 0;
}

.cl-btn-6-in:before {
  top: calc(50% - 4px);
  transform: rotate(45deg);
}

.cl-btn-6-in:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.cl-btn-6:hover .cl-btn-6-txt {
  opacity: 1;
}

.cl-btn-6:hover .cl-btn-6-in:before, .cl-btn-6:hover .cl-btn-6-in:after {
  transform: rotate(0);
}

.cl-btn-6:hover .cl-btn-6-in:before {
  top: 0;
}

.cl-btn-6:hover .cl-btn-6-in:after {
  bottom: 0;
}

